  
  .form {
    background-color: #15172b;
    border-radius: 20px;
    box-sizing: border-box;
    height: 500px;
    padding: 20px;
    width: 500px;
    margin: auto;
  }

  .textarea{
    height: 150px;
    margin-bottom: 30px;
    font-size: 18px;
  }
  
  .title {
    color: #eee;
    /* font-family: sans-serif; */
    font-size: 36px;
    font-weight: 600;
    margin-top: 30px;
  }
  
  .input-container {
    height: 50px;
    position: relative;
    width: 100%;
  }
  
  .ic1 {
    margin-top: 40px;
  }
  
  .ic2 {
    margin-top: 30px;
    margin-bottom: 20px;
  }

  
  .input {
    background-color: #303245;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    font-size: 18px;
    height: 100%;
    outline: 0;
    padding: 4px 20px 0;
    width: 100%;
    font-family: sans-serif;
  }
  

  .cut-short {
    width: 50px;
  }
  
  .submit {
    background-color: #08d;
    border-radius: 12px;
    border: 0;
    box-sizing: border-box;
    color: #eee;
    cursor: pointer;
    font-size: 18px;
    height: 50px;
    margin-top: 14px;
    text-align: center;
    width: 100%;
  }
  
  .submit:active {
    background-color: #06b;
  }